import { createHashRouter } from "react-router-dom";
import App from "./App";
import Home from "./routes/Home";
import Search from "./routes/Search";
import Tv from "./routes/Tv";

/**
 * ghpage   react-router-dom createHashRouter 
 * createHashRouter  root url # 
 *  createBrowserRouter  
 */
const router = createHashRouter([
    {
        path: `/`,
        element: <App />,
        children: [
            {
                path: "",
                element: <Home />,
            },
            {
                path: "movies/:movieId",
                element: <Home />,
            },
            {
                path: "tv",
                element: <Tv />,
            },
            {
                path: "search",
                element: <Search />,
            },
        ],
    }
]);

export default router;